import react from "react";

function HomePage() {
    return (
      <div className="home-page">
        <div className="statistics-panel">
          <h3>Statistics Panel</h3>
          <p>Display some statistics here...</p>
        </div>
        <div className="home-alert-subpanels-container">
          <div className="alert-subpanel pending-messages">
            <h3>Pending Messages</h3>
            <p>Messages content...</p>
          </div>
          <div className="alert-subpanel recommendations-pending-approval">
            <h3>Recommendations Pending Approval</h3>
            <p>Recommendations content...</p>
          </div>
          <div className="alert-subpanel timeline-alerts">
            <h3>Timeline Alerts</h3>
            <p>Timeline alerts content...</p>
          </div>
        </div>
      </div>
    );
  }

  export default HomePage;