import React, { useState } from 'react';
import ModalForm from './ClientPageComponents/ModalForm'; // Form to add new client
import ClientDetailModal from './ClientPageComponents/ClientDetailModal'; // Modal to display client details
import './ClientPage.css';

function ClientPage() {
  const [clients, setClients] = useState([]); // Store the clients
  const [isModalOpen, setIsModalOpen] = useState(false); // For the form modal
  const [selectedClient, setSelectedClient] = useState(null); // For selected client details modal

  // Add a new client to the clients list
  const handleAddClient = (newClient) => {
    setClients((prevClients) => [...prevClients, newClient]);
  };

  // Open the modal to add a new client
  const openModal = () => {
    setIsModalOpen(true);
  };

  // Close the modal to add a new client
  const closeModal = () => {
    setIsModalOpen(false);
  };

  // Open client detail modal when clicking on a client card
  const handleClientCardClick = (client) => {
    setSelectedClient(client);
  };

  // Close client details modal
  const closeClientDetailModal = () => {
    setSelectedClient(null);
  };

  // Update client information
  const handleUpdateClient = (updatedClient) => {
    setClients((prevClients) =>
      prevClients.map((client) =>
        client.id === updatedClient.id ? updatedClient : client
      )
    );
  };

  return (
    <div className="clients-page">
      <h2>Clients</h2>
      <button onClick={openModal} className="add-client-button">Add Client</button>

      {/* Client Cards */}
      <div className="client-list">
        {clients.map((client) => (
          <div key={client.id} className="client-card" onClick={() => handleClientCardClick(client)}>
            <h3>{client.name}</h3>
            <p>Age: {client.age}</p>
            <p>Gender: {client.gender}</p>
            <p>Goal: {client.goals}</p>

            {/* Display array contents */}
            <p><strong>Messages:</strong> {client.messages.length ? client.messages.join(', ') : 'No messages'}</p>
            <p><strong>Approvals:</strong> {client.systemRecommendationApproval.length ? client.systemRecommendationApproval.join(', ') : 'No approvals'}</p>
            <p><strong>Timeline:</strong> {client.timeline.length ? client.timeline.join(', ') : 'No events'}</p>
          </div>
        ))}
      </div>

      {/* Modal to Add New Client */}
      <ModalForm
        isOpen={isModalOpen}
        closeModal={closeModal}
        handleAddClient={handleAddClient}
      />

      {/* Client Detail Modal */}
      {selectedClient && (
        <ClientDetailModal
          client={selectedClient}
          onClose={closeClientDetailModal}
          handleUpdateClient={handleUpdateClient}  /* Pass down handleUpdateClient */
        />
      )}
    </div>
  );
}

export default ClientPage;
