import React, { useState } from 'react';
import './ModalForm.css';

function ModalForm({ isOpen, closeModal, handleAddClient }) {
  const [clientDetails, setClientDetails] = useState({
    name: '',
    age: '',
    goals: '',
    height: '',
    weight: '',
    gender: '',
    dietaryRestrictions: '',
    sizes: {
      pelvis: '',
      hips: '',
      chest: '',
      neck: '',
      navel: '',
      thigh: '',
      arm: ''
    },
    bodyComposition: {
      RMR: '',
      NEATCoefficient: '',
      NEAT: '',
      TDEE: '',
      PAL: ''
    },
    health: {
      conditions: '',
      recentBloodTestValues: '',
      currentMedications: '',
      supplements: ''
    },
    messages: [],
    systemRecommendationApproval: [],
    timeline: [],
    menus: [],
    supplementMenu: []
  });

  const calculateBMI = () => {
    if (clientDetails.height && clientDetails.weight) {
      const heightInMeters = clientDetails.height / 100; // Assuming height is in cm
      return (clientDetails.weight / (heightInMeters * heightInMeters)).toFixed(2);
    }
    return '';
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    setClientDetails((prevDetails) => ({
      ...prevDetails,
      [name]: value
    }));
  };

  const handleSizeChange = (e) => {
    const { name, value } = e.target;
    setClientDetails((prevDetails) => ({
      ...prevDetails,
      sizes: { ...prevDetails.sizes, [name]: value }
    }));
  };

  const handleBodyCompChange = (e) => {
    const { name, value } = e.target;
    setClientDetails((prevDetails) => ({
      ...prevDetails,
      bodyComposition: { ...prevDetails.bodyComposition, [name]: value }
    }));
  };

  const handleHealthChange = (e) => {
    const { name, value } = e.target;
    setClientDetails((prevDetails) => ({
      ...prevDetails,
      health: { ...prevDetails.health, [name]: value }
    }));
  };

  const handleSubmit = (e) => {
    e.preventDefault();

    const newClient = {
      id: Date.now(),
      name: clientDetails.name,
      age: clientDetails.age,
      goals: clientDetails.goals,
      height: clientDetails.height,
      weight: clientDetails.weight,
      bmi: calculateBMI(),
      gender: clientDetails.gender,
      dietaryRestrictions: clientDetails.dietaryRestrictions,
      sizes: clientDetails.sizes,
      bodyComposition: clientDetails.bodyComposition,
      health: clientDetails.health,
      messages: [],  // Empty messages array
      timeline: [],  // Empty timeline array
      systemRecommendationApproval: [],  // Empty approvals array
      menus: [],
      supplementMenu: []
    };

    handleAddClient(newClient);
    closeModal();
  };

  if (!isOpen) return null;

  return (
    <div className="modal-overlay">
      <div className="modal-content">
        <h2>Add New Client</h2>
        <form onSubmit={handleSubmit}>
          <label>Name:</label>
          <input
            type="text"
            name="name"
            value={clientDetails.name}
            onChange={handleChange}
            required
          />
          <label>Age:</label>
          <input
            type="number"
            name="age"
            value={clientDetails.age}
            onChange={handleChange}
          />
          <label>Goals:</label>
          <textarea
            name="goals"
            value={clientDetails.goals}
            onChange={handleChange}
          />
          <label>Height (cm):</label>
          <input
            type="number"
            name="height"
            value={clientDetails.height}
            onChange={handleChange}
          />
          <label>Weight (kg):</label>
          <input
            type="number"
            name="weight"
            value={clientDetails.weight}
            onChange={handleChange}
          />
          <label>Gender:</label>
          <select
            name="gender"
            value={clientDetails.gender}
            onChange={handleChange}
          >
            <option value="">Select Gender</option>
            <option value="Male">Male</option>
            <option value="Female">Female</option>
            <option value="Other">Other</option>
          </select>
          <label>Dietary Restrictions:</label>
          <textarea
            name="dietaryRestrictions"
            value={clientDetails.dietaryRestrictions}
            onChange={handleChange}
          />

          {/* Sizes */}
          <h3>Sizes</h3>
          {Object.keys(clientDetails.sizes).map((key) => (
            <div key={key}>
              <label>{key.charAt(0).toUpperCase() + key.slice(1)}:</label>
              <input
                type="text"
                name={key}
                value={clientDetails.sizes[key]}
                onChange={handleSizeChange}
              />
            </div>
          ))}

          {/* Body Composition */}
          <h3>Body Composition</h3>
          {Object.keys(clientDetails.bodyComposition).map((key) => (
            <div key={key}>
              <label>{key}:</label>
              <input
                type="text"
                name={key}
                value={clientDetails.bodyComposition[key]}
                onChange={handleBodyCompChange}
              />
            </div>
          ))}

          {/* Health Information */}
          <h3>Health Information</h3>
          {Object.keys(clientDetails.health).map((key) => (
            <div key={key}>
              <label>{key.replace(/([A-Z])/g, ' $1')}:</label>
              <textarea
                name={key}
                value={clientDetails.health[key]}
                onChange={handleHealthChange}
              />
            </div>
          ))}

          <button type="submit">Add Client</button>
        </form>
        <button onClick={closeModal}>Close</button>
      </div>
    </div>
  );
}

export default ModalForm;
