import React, { useState } from 'react';
import './FoodInputForm.css';
import MealSelect from './MealSelect';

function FoodInputForm({ handleAddFood, mealForFood, setMealForFood, overallMenu }) {
  const [foodDetails, setFoodDetails] = useState({
    name: '',
    category: '',
    cookingStyle: '',
    protein: '',
    oil: '',
    calories: '',
  });

  const handleChange = (e) => {
    setFoodDetails({
      ...foodDetails,
      [e.target.name]: e.target.value,
    });
  };

  const handleSubmit = (e) => {
    e.preventDefault(); // Prevent form reload
    if (foodDetails.name && foodDetails.category && foodDetails.cookingStyle) {
      // Pass the complete food details to the parent via handleAddFood
      handleAddFood(foodDetails); // Ensure this is called with the correct food details
  
      // Reset form fields after submitting
      setFoodDetails({
        name: '',
        category: '',
        cookingStyle: '',
        protein: '',
        oil: '',
        calories: '',
      });
    } else {
      alert('Please fill out all required fields.');
    }
  };
  
  

  return (
    <div className="food-input-form">
      {/* Choose Meal for Food */}
      <label>Choose Meal for Food</label>
      <select
  value={mealForFood}
  onChange={(e) => setMealForFood(e.target.value)}  // Set `mealForFood` to `id`
>
  <option value="">Select Meal</option>
  {Array.isArray(overallMenu) && overallMenu.length > 0 ? (
    overallMenu.map((meal) => (
      <option key={meal.id} value={meal.id}>  {/* Value is `meal.id` */}
        {meal.name}
      </option>
    ))
  ) : (
    <option disabled>No meals available</option>
  )}
</select>

      {/* Meal Category */}
      <label>Meal Category</label>
      <select
        name="category"
        value={foodDetails.category}
        onChange={handleChange}
      >
        <option value="">Select Category</option>
        {['Protein', 'Fat', 'Veggies', 'Carbs', 'Drinks', 'Snack'].map((cat) => (
          <option key={cat} value={cat}>
            {cat}
          </option>
        ))}
      </select>

      {/* Food Name */}
      <label>Food Name</label>
      <input
        type="text"
        name="name"
        value={foodDetails.name}
        onChange={handleChange}
      />

      {/* Cooking Style */}
      <label>Cooking Style</label>
      <select
        name="cookingStyle"
        value={foodDetails.cookingStyle}
        onChange={handleChange}
      >
        <option value="">Select Cooking Style</option>
        {['Fried', 'Deep Fried', 'Baked', 'Boiled', 'Grilled', 'Steamed'].map(
          (style) => (
            <option key={style} value={style}>
              {style}
            </option>
          )
        )}
      </select>

      {/* Protein */}
      <label>Protein (g)</label>
      <input
        type="number"
        name="protein"
        value={foodDetails.protein}
        onChange={handleChange}
      />

      {/* Oil */}
      <label>Oil</label>
      <input
        type="text"
        name="oil"
        value={foodDetails.oil}
        onChange={handleChange}
      />

      {/* Calories */}
      <label>Calories</label>
      <input
        type="number"
        name="calories"
        value={foodDetails.calories}
        onChange={handleChange}
      />

      {/* Submit Button */}
      <button onClick={handleSubmit} className="add-food-button">
        Add Food
      </button>
    </div>
  );
}

export default FoodInputForm;
