import React from "react";
import "./WorkspacePage.css";

function WorkspacePage({ setCurrentPage }) {
  const handleMenuCreationClick = () => {
    if (typeof setCurrentPage === "function") {
      setCurrentPage("menuCreation"); // Navigate to the Menu Creation page
    } else {
      console.error("setCurrentPage is not a function");
    }
  };

  return (
    <div className="workspace">
      <h2>Workspace</h2>
      <div className="workspace-panels">
        {/* Storage Panel */}
        <div className="workspace-panel storage">
          <h3>Storage Panel</h3>
          <p>Track your ingredient stock and manage inventory here.</p>
        </div>

        {/* Menu Storage Panel */}
        <div className="workspace-panel menu-storage">
          <h3>Menu Storage</h3>
          <p>View, organize, and edit your saved menus.</p>
        </div>

        {/* Supplement Balance Panel */}
        <div className="workspace-panel supplement-balance">
          <h3>Supplement Balance</h3>
          <p>Balance supplements and track nutrient levels.</p>
        </div>
      </div>

      {/* Create New Menu Button */}
      <div className="create-menu-button-container">
        <button
          className="create-menu-button"
          onClick={handleMenuCreationClick}
        >
          Create New Menu
        </button>
      </div>
    </div>
  );
}

export default WorkspacePage;
