import React, { useState } from 'react';
import './App.css';

// Importing necessary DnD components
import { DndProvider } from 'react-dnd';
import { HTML5Backend } from 'react-dnd-html5-backend';

// Importing components
import Sidebar from './components/Sidebar';
import MeetingCalendarPage from './components/MeetingCalendarPage';
import ClientPage from './components/ClientPage';
import TopBar from './components/TopBar';
import HomePage from './components/HomePage';
import AlertsPage from './components/AlertsPage';
import WorkspacePage from './components/WorkspacePage';
import SubscriptionsPage from './components/SubscriptionsPage';
import ProfilePage from './components/ProfilePage';
import LoginPage from './components/LoginPage'; // Import the login page
import MenuCreationPage from './components/MenuCreationPage'; // Import the Menu Creation page

// Main App Component
function App() {
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const [currentPage, setCurrentPage] = useState('home');

  const handleLogin = () => {
    setIsLoggedIn(true);
  };

  let content;
  switch (currentPage) {
    case 'home':
      content = <HomePage />;
      break;
    case 'alerts':
      content = <AlertsPage />;
      break;
      case "workspace":
        content = <WorkspacePage setCurrentPage={setCurrentPage} />;
        break;
    case 'subscriptions':
      content = <SubscriptionsPage />;
      break;
    case 'profile':
      content = <ProfilePage />;
      break;
    case 'clients':
      content = <ClientPage />;
      break;
    case 'calendar':
      content = <MeetingCalendarPage />;
      break;
    case 'menuCreation': // Add the case for menu creation
      content = <MenuCreationPage />;
      break;
    default:
      content = <HomePage />;
  }

  if (!isLoggedIn) {
    return <LoginPage onLogin={handleLogin} />;
  }

  return (
    <DndProvider backend={HTML5Backend}>
      <div className="app">
        <TopBar />
        <div className="main-content">
          <Sidebar setCurrentPage={setCurrentPage} />
          <div className="central-area">{content}</div>
        </div>
      </div>
    </DndProvider>
  );
}

export default App;
