import React, { useState } from 'react';
import MealItem from './MenuCreationComponents/MealItem';
import MealSelect from './MenuCreationComponents/MealSelect';
import FoodInputForm from './MenuCreationComponents/FoodInputForm';
import CreateNewMeal from './MenuCreationComponents/CreateNewMeal'; // Import the new component
import './MenuCreationPage.css';

//when working with a pregnerated ai menu and wanting to edit it. we need to change the overallMenu variable to habe the new meals and food
const initialMeals = [
    { id: 1, name: 'Breakfast' },
    { id: 2, name: 'Lunch' },
    { id: 3, name: 'Dinner' },
    { id: 4, name: 'Snack' }
  ];
  

  function MenuCreationPage() {
    const [mealType, setMealType] = useState('');
    const [mealForFood, setMealForFood] = useState('');
    const [overallMenu, setOverallMenu] = useState(
      initialMeals.map((meal) => ({ ...meal, foods: [] }))
    );
  
    // Handle adding a new meal
    const handleAddMeal = () => {
      if (!mealType) {
        alert('Please select a meal type');
        return;
      }
  
      const newMeal = { id: Date.now(), name: mealType, foods: [] };  // Using `Date.now()` as a simple unique ID generator
      setOverallMenu([...overallMenu, newMeal]);
      setMealType('');
    };
  
    // Handle adding a new food item to a meal
    const handleAddFood = (foodDetails) => {
      if (!mealForFood) {
        alert('Please select a meal for the food');
        return;
      }
  
      // Update the menu using the meal `id`
      const updatedMenu = overallMenu.map((meal) => {
        if (Number(meal.id) === Number(mealForFood)) {
          return { ...meal, foods: [...meal.foods, foodDetails] };
        }
        return meal;
      });
  
      setOverallMenu(updatedMenu); // Update the state
    };
  
    // Handle removing a food item from a meal
    const handleRemoveFood = (mealId, foodIndex) => {
      const updatedMenu = overallMenu.map((meal) => {
        if (meal.id === mealId) {
          const updatedFoods = meal.foods.filter((_, index) => index !== foodIndex);
          return { ...meal, foods: updatedFoods };
        }
        return meal;
      });
      setOverallMenu(updatedMenu);
    };
  
    // Handle removing a meal and all its foods
    const handleRemoveMeal = (mealId) => {
      const updatedMenu = overallMenu.filter((meal) => meal.id !== mealId);  // Compare by `id`
      setOverallMenu(updatedMenu);
    };
  
    // Handle moving a meal up in the order
    const handleMoveMealUp = (mealId) => {
      const mealIndex = overallMenu.findIndex(meal => meal.id === mealId);
      if (mealIndex > 0) {
        const updatedMenu = [...overallMenu];
        const [mealToMove] = updatedMenu.splice(mealIndex, 1);
        updatedMenu.splice(mealIndex - 1, 0, mealToMove); // Insert it before the previous meal
        setOverallMenu(updatedMenu);
      }
    };
  
    // Handle moving a meal down in the order
    const handleMoveMealDown = (mealId) => {
      const mealIndex = overallMenu.findIndex(meal => meal.id === mealId);
      if (mealIndex < overallMenu.length - 1) {
        const updatedMenu = [...overallMenu];
        const [mealToMove] = updatedMenu.splice(mealIndex, 1);
        updatedMenu.splice(mealIndex + 1, 0, mealToMove); // Insert it after the next meal
        setOverallMenu(updatedMenu);
      }
    };
  
    return (
      <div className="menu-creation-page">
        <h2>Menu Creation</h2>
  
        <div className="menu-creation-content">
          {/* Left: Food Input Form */}
          <div className="food-input-form-container">
            <FoodInputForm
              handleAddFood={handleAddFood}
              mealForFood={mealForFood}
              setMealForFood={setMealForFood}
              overallMenu={overallMenu}
            />
          </div>
  
          {/* Right: Display Overall Menu */}
          <div className="overall-menu">
            <h3>Overall Menu</h3>
            <div className="menu-card">
              {overallMenu.map((meal) => (
                <MealItem
                  key={meal.id} // Use `meal.id` as the unique key
                  meal={meal}
                  handleRemoveFood={handleRemoveFood}
                  removeMeal={handleRemoveMeal}
                  handleMoveMealUp={handleMoveMealUp}
                  handleMoveMealDown={handleMoveMealDown}
                />
              ))}
            </div>
          </div>
        </div>
  
        {/* Meal Type Selection (Moved to the bottom) */}
        <div className="create-new-meal-wrapper">
          <CreateNewMeal
            mealType={mealType}
            setMealType={setMealType}
            handleAddMeal={handleAddMeal}
          />
        </div>
      </div>
    );
  }
  
export default MenuCreationPage;
