import React from 'react';

function Sidebar({ setCurrentPage }) {
  return (
    <div className="sidebar">
      <button className="sidebar-button" onClick={() => setCurrentPage("home")}>
        Home
      </button>
      <button className="sidebar-button" onClick={() => setCurrentPage("alerts")}>
        Alerts
      </button>
      <button className="sidebar-button" onClick={() => setCurrentPage("workspace")}>
        Workspace
      </button>
      <button
        className="sidebar-button"
        onClick={() => setCurrentPage("subscriptions")}
      >
        Subscriptions
      </button>
      <button className="sidebar-button" onClick={() => setCurrentPage("profile")}>
        Profile
      </button>
      <button className="sidebar-button" onClick={() => setCurrentPage("clients")}>
        Clients
      </button>
      <button
        className="sidebar-button"
        onClick={() => setCurrentPage("calendar")}
      >
        Meeting Calendar
      </button>
      <button
        className="sidebar-button"
        onClick={() => setCurrentPage("menuCreation")} // Set page to Menu Creation
      >
        Create New Menu
      </button>
    </div>
  );
}

export default Sidebar;
