import React from 'react';
import './MealItem.css';

const MealItem = ({ meal, handleRemoveFood, removeMeal, handleMoveMealUp, handleMoveMealDown }) => {
  const categorizedFoods = meal.foods.reduce((categories, food) => {
    if (!categories[food.category]) {
      categories[food.category] = [];
    }
    categories[food.category].push(food);
    return categories;
  }, {});

  return (
    <div className="meal-item">
      <h3>{meal.name}</h3>

      {/* Move Up and Move Down buttons */}
      <div className="move-buttons">
        <button 
          className="move-up-button" 
          onClick={() => handleMoveMealUp(meal.id)}
        >
          Move Up
        </button>
        <button 
          className="move-down-button" 
          onClick={() => handleMoveMealDown(meal.id)}
        >
          Move Down
        </button>
      </div>

      {/* Remove Meal button */}
      <button
        className="remove-meal-button"
        onClick={() => removeMeal(meal.id)}  // Use `id` here
      >
        Remove Meal
      </button>

      {Object.keys(categorizedFoods).map((category) => {
        const foodsInCategory = categorizedFoods[category];
        if (foodsInCategory.length > 0) {
          return (
            <div key={category} className="meal-category">
              <h4>{category.charAt(0).toUpperCase() + category.slice(1)}</h4>
              <ul className="food-list">
                {foodsInCategory.map((food, idx) => (
                  <li key={idx} className="food-item">
                    <span>
                      <strong>{food.name}</strong> ({food.cookingStyle}) - 
                      {food.calories} cal, {food.protein}g protein, {food.oil}g fat
                    </span>
                    <button
                      className="remove-food-button"
                      onClick={() => handleRemoveFood(meal.id, idx)}  // Use `id` here
                    >
                      Remove
                    </button>
                  </li>
                ))}
              </ul>
            </div>
          );
        }
        return null;
      })}
    </div>
  );
};

export default MealItem;
