import React from 'react';
import './MealSelect.css';

function CreateNewMeal({ mealType, setMealType, handleAddMeal }) {
    const predefinedMeals = ['Breakfast', 'Lunch', 'Dinner', 'Snack'];
  
    return (
      <div className="meal-select">
        <label>Create New Meal</label>
        <select 
          value={mealType}
          onChange={(e) => setMealType(e.target.value)}
        >
          <option value="">Select Meal Type</option>
          {predefinedMeals.map((meal) => (
            <option key={meal} value={meal}>
              {meal}
            </option>
          ))}
        </select>
  
        <button onClick={handleAddMeal} className="add-meal-button">Add Meal</button>
      </div>
    );
  }
  

export default CreateNewMeal;
