import react from "react";

function SubscriptionsPage() {
    return (
      <div className="subscriptions">
        <h2>Service Subscription Plans</h2>
        <p>Select the type of subscription that suits your professional practice as a dietitian:</p>
        
        <div className="subscription-options">
          <div className="subscription-option">
            <h3>Basic Plan</h3>
            <p>Access to standard tools for managing a small client base, including meal planning templates and basic analytics.</p>
            <button className="subscription-button">Choose Basic</button>
          </div>
          <div className="subscription-option">
            <h3>Premium Plan</h3>
            <p>Advanced tools for client management, personalized meal plans, and in-depth analytics for up to 100 clients.</p>
            <button className="subscription-button">Choose Premium</button>
          </div>
          <div className="subscription-option">
            <h3>Enterprise Plan</h3>
            <p>Full suite of tools, unlimited client management, team collaboration features, and priority support.</p>
            <button className="subscription-button">Choose Enterprise</button>
          </div>
        </div>
      </div>
    );
  }

  export default SubscriptionsPage;