import react from "react";

function AlertsPage() {
    return (
      <div className="alerts-page">
        <div className="alert-subpanels-container">
          <div className="alert-subpanel pending-messages">
            <h3>Pending Messages</h3>
            <p>Messages content...</p>
          </div>
          <div className="alert-subpanel recommendation-approvals">
            <h3>Recommendation Approvals</h3>
            <p>Approval content...</p>
          </div>
          <div className="alert-subpanel timeline-alerts">
            <h3>Timeline Alerts</h3>
            <p>Alerts content...</p>
          </div>
        </div>
      </div>
    );
  }

  export default AlertsPage;