import React, { useState } from 'react';
import Calendar from "react-calendar";
import "react-calendar/dist/Calendar.css"; // Import calendar styles
import clients from './clientsData'; // Import the clients data

function MeetingCalendarPage() {
  // Initialize meetings state as an empty array
  const [meetings, setMeetings] = useState([]);

  // State for new meeting form
  const [newMeeting, setNewMeeting] = useState({
    title: '',
    date: '',
    time: '',
    clientId: '', // Store clientId
    clientName: '', // Store clientName
  });

  // Handle form input change
  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setNewMeeting((prevMeeting) => ({
      ...prevMeeting,
      [name]: value,
    }));
  };

  // Update clientId and clientName when a client is selected
  const handleClientChange = (e) => {
    const selectedClientId = e.target.value;
    const selectedClient = clients.find(client => client.id === Number(selectedClientId)); // Ensure client.id is a number

    setNewMeeting((prevMeeting) => ({
      ...prevMeeting,
      clientId: selectedClientId,
      clientName: selectedClient ? selectedClient.name : '', // Set client name
    }));
  };

  // Add a new meeting
  const addMeeting = (e) => {
    e.preventDefault();
    if (newMeeting.title && newMeeting.date && newMeeting.time && newMeeting.clientId) {
      setMeetings((prevMeetings) => [...prevMeetings, newMeeting]);
      setNewMeeting({ title: '', date: '', time: '', clientId: '', clientName: '' }); // Reset form after adding meeting
    } else {
      alert('Please fill out all fields.');
    }
  };

  // Generate time options in increments of 5 minutes
  const generateTimeOptions = () => {
    const options = [];
    for (let hour = 0; hour < 24; hour++) {
      for (let minute = 0; minute < 60; minute += 5) {
        const formattedTime = `${hour < 10 ? '0' : ''}${hour}:${minute < 10 ? '0' : ''}${minute}`;
        options.push(formattedTime);
      }
    }
    return options;
  };

  // Convert meetings to the format needed by the calendar
  const calendarEvents = meetings.map((meeting) => ({
    date: new Date(meeting.date), // Convert date string to Date object
    title: meeting.title, // Store title separately
    clientId: meeting.clientId, // Store clientId
  }));

  return (
    <div className="calendar-page">
      <h2>Meeting Calendar</h2>
      <p>Schedule and manage your meetings with clients here.</p>

      {/* Add Meeting Form */}
      <form onSubmit={addMeeting} className="meeting-form">
        <div>
          <label>
            Meeting Title:
            <input
              type="text"
              name="title"
              value={newMeeting.title}
              onChange={handleInputChange}
              placeholder="Enter meeting title"
              required
            />
          </label>
        </div>
        <div>
          <label>
            Date:
            <input
              type="date"
              name="date"
              value={newMeeting.date}
              onChange={handleInputChange}
              required
            />
          </label>
        </div>
        <div>
          <label>
            Time:
            <select
              name="time"
              value={newMeeting.time}
              onChange={handleInputChange}
              required
            >
              <option value="" disabled>Select a time</option>
              {generateTimeOptions().map((time, index) => (
                <option key={index} value={time}>
                  {time}
                </option>
              ))}
            </select>
          </label>
        </div>
        <div>
          <label>
            Client:
            <select
              name="clientId"
              value={newMeeting.clientId}
              onChange={handleClientChange}
              required
            >
              <option value="" disabled>Select a client</option>
              {clients.map((client) => (
                <option key={client.id} value={client.id}>
                  {client.name}
                </option>
              ))}
            </select>
          </label>
        </div>
        <button type="submit" className="add-meeting-button">
          Add Meeting
        </button>
      </form>

      {/* Display the calendar */}
      <div className="calendar-container">
        <Calendar
          onChange={() => {}}
          value={new Date()} // Set the current date as the default selected date
          tileContent={({ date, view }) => {
            // Show meeting events on the calendar dates
            const eventsForDate = calendarEvents.filter(
              (event) => event.date.toDateString() === date.toDateString()
            );
            return (
              eventsForDate.length > 0 && (
                <ul>
                  {eventsForDate.map((event, index) => {
                    // Find the client by matching the clientId
                    const client = clients.find(client => Number(client.id) === Number(event.clientId)); // Ensure comparison uses numbers
                    return (
                      <li key={index}>
                        <strong>{event.title}</strong><br />
                        <em>{client ? client.name : 'Unknown Client'}</em> {/* Display the client name */}
                      </li>
                    );
                  })}
                </ul>
              )
            );
          }}
        />
      </div>

      {/* Meetings List */}
      <div className="meeting-list">
        <h3>Scheduled Meetings</h3>
        {meetings.length === 0 ? (
          <p>No meetings scheduled yet.</p>
        ) : (
          <ul>
            {meetings.map((meeting, index) => {
              // Find the client by matching the clientId
              const client = clients.find(client => Number(client.id) === Number(meeting.clientId)); // Ensure comparison uses numbers
              return (
                <li key={index}>
                  <strong>{meeting.title}</strong> - {meeting.date} at {meeting.time} with{' '}
                  <em>{client ? `${client.name} (Age: ${client.age})` : 'Unknown Client'}</em>
                </li>
              );
            })}
          </ul>
        )}
      </div>
    </div>
  );
}

export default MeetingCalendarPage;
